import { ref, onMounted } from 'vue'

// eslint-disable-next-line max-lines-per-function
function tagl() {
	const taglist = ref([
		{
			name: 'vue',
			color: ''
		},
		{
			name: 'vue-cli',
			color: ''
		},
		{
			name: 'vue-router',
			color: ''
		},
		{
			name: 'vuex',
			color: ''
		},
		{
			name: 'vue-request',
			color: ''
		},
		{
			name: 'axios',
			color: ''
		},
		{
			name: 'vue3',
			color: ''
		},
		{
			name: 'vue2源码',
			color: ''
		},
		{
			name: 'vue3源码',
			color: ''
		},
		{
			name: 'tsx',
			color: ''
		},
		{
			name: 'ts',
			color: ''
		},
		{
			name: 'scss',
			color: ''
		},
		{
			name: 'css',
			color: ''
		},
		{
			name: 'vite',
			color: ''
		},
		{
			name: 'vuepress',
			color: ''
		},
		{
			name: 'react',
			color: ''
		},
		{
			name: 'react-router',
			color: ''
		},
		{
			name: '高德地图心得',
			color: ''
		},
		{
			name: '低代码',
			color: ''
		},
		{
			name: 'nest',
			color: ''
		},
		{
			name: 'nuxt',
			color: ''
		},
		{
			name: 'flutter',
			color: ''
		},
		{
			name: 'flutter-web',
			color: ''
		},
		{
			name: 'flutter-native',
			color: ''
		},
		{
			name: 'threeJS',
			color: ''
		},
		{
			name: '动画',
			color: ''
		},
		{
			name: 'js',
			color: ''
		},
		{
			name: '直播',
			color: ''
		}
	])

	function color16() {
		const r = Math.floor(Math.random() * 256)
		const g = Math.floor(Math.random() * 256)
		const b = Math.floor(Math.random() * 256)
		const color = 'rgb(' + r + ',' + g + ',' + b + ')'
		return color
	}

	function setTagColor() {
		taglist.value.forEach((item) => {
			item.color = color16()
		})
	}

	onMounted(() => {
		setTagColor()
	})

	return {
		taglist
	}
}

export default tagl
